<template>
  <div class="template-container">
    <div class="template-container-title">编辑广告创意</div>
    <el-form ref="form" :model="form" :inline="true">
      <!--推广计划 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">推广计划</div>
        <div class="temp-item-main">
          <el-form-item label="所属推广计划">
            <el-select @change="campaignChange" disabled v-model="form.campaign_id" filterable remote placeholder="选择推广计划" :remote-method="remoteCampaign" :loading="loadingCampaign">
              <el-option v-for="item in adCampaignsList" :key="item.campaign_id" :data-type="item.product_type" :label="item.campaign_name" :value="item.campaign_id"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </div>
      <!--推广计划 end -->

      <!--站点 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">站点</div>
        <div class="temp-item-main">
          <label class="temp-item-label" for="">投放媒体</label>
          <div class="radio-group-inlineblock">
            <el-radio-group @change="siteSetListChange" v-model="form.site_set">
              <el-radio disabled class="radio-group-item" v-for="(val,key) in site_setList" :key="key" :label="key">{{val}}</el-radio>
            </el-radio-group>
          </div>
        </div>
      </div>
      <!--站点 end -->

      <!--目标详情 begin -->
      <div class="temp-item-block" v-show='productShow'>
        <div class="temp-item-title">目标详情</div>
        <div class="temp-item-main">
          <el-form-item label="标的物">
            <el-select disabled @change="productChange" v-model="form.product_refs_id" filterable placeholder="选择标的物">
              <el-option v-for="item in productList" :key="item.product_refs_id" :label="item.product_name" :value="item.product_refs_id"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </div>
      <!--目标详情 end -->

      <!--落地页 begin -->
      <div class="temp-item-block" v-show='destinationUrlShow'>
        <div class="temp-item-title">落地页</div>
        <div class="temp-item-main">
          <el-form-item label="落地页url">
            <input disabled class="temp-long-inputtext" v-model="form.destination_url" type="text" value="" maxlength="1023" />
          </el-form-item>
        </div>
      </div>
      <!--落地页 end -->

      <!--创意规格 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">创意规格</div>
        <div class="temp-item-main" style="display:inline-flex">
          <el-table ref="adcreativeTable" v-loading="loading" :data="adcreativeList" @current-change="handleCurrentChange" max-height="556" highlight-current-row style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
            <el-table-column fixed prop="adcreative_template_id" label="ID" sortable>
            </el-table-column>
            <!-- <el-table-column :render-header="tableHeaderRender" prop="crt_style" label="创意形式" :formatter="formatterCrtstyle">
            </el-table-column> -->
            <el-table-column prop="adcreative_template_name" label="广告版位">
            </el-table-column>
            <el-table-column filter-placement="bottom-start" :filter-method="filterHandler" :filters="element_type_filter" prop="adcreative_template_style" label="创意形式" :formatter="formatterCrtstyle">
            </el-table-column>
            <el-table-column prop="adcreative_template_desc" label="创意规格描述">
            </el-table-column>
          </el-table>
          <div v-if="adcreativeTemplateInfo_row.adcreative_sample_image.image" style="float:right; width:200px;">
            <img style="width:100%" :src="adcreativeTemplateInfo_row.adcreative_sample_image.image" />
            <div class="sample_image_info">
              <p>{{ adcreativeTemplateInfo_row.adcreative_sample_image.name }} <br>
                <span>(广告可能出现在以上位置)</span>
              </p>
              <span>{{ adcreativeTemplateInfo_row.adcreative_template_size}}</span>
            </div>
          </div>
        </div>
      </div>
      <!--创意规格 end -->

      <!--上传创意 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">上传创意</div>
        <div class="temp-item-main">
          <el-tabs v-model="tabActiveName" @tab-click="handleTbaClick">
            <el-tab-pane v-for="(tabItem,tabIndex) in tabList" :key="tabItem.name" :label="tabItem.label" :name="tabItem.name">
              <div class="uploadurl-input">
                <el-input placeholder="请输入创意名称" name="adcreative_name" v-model="tabItem.adcreative_name" clearable></el-input>
              </div>
              <!--  视频 begin -->
              <div v-if="tabItem.elementsData.videoList.length > 0 " class="uploadList-wrap">
                <!-- <el-upload class="upload-demo" v-for="(uploadItem, index) in tabItem.elementsData.videoList" :name="uploadItem.name" :key="uploadItem.name+index" :before-upload="beforeUpload" :data="upadlodData" :headers="uploadHeaders" :on-preview="uploadFile" drag action="/Admin/Marketing_Images/add">
                  <img style="display:none" v-bind:src="imgurl" />
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">
                    将文件拖到此处，或
                    <em>点击上传</em>
                    <br>
                    <p class="upload-size">
                      {{ uploadItem.restriction.video_restriction.min_width }}px * {{ uploadItem.restriction.video_restriction.min_height }}px <br>
                      <span>（推荐尺寸）</span>
                    </p>
                  </div>
                  <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.video_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.video_restriction.file_size }}kb</div>
                </el-upload> -->
                <div class="uploadList-li" v-for="(uploadItem, index ) in tabItem.elementsData.videoList" :key="uploadItem.name+index">
                  <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'video')" type="file" />
                  <div class="uploadfile-item">
                    <!-- <img class="uploadfile-preview" :src="uploadItem.preview_url" /> -->
                    <div>
                      <i class="el-icon-upload icon-upload"></i>
                      <div class="el-upload__text">
                        将视频文件拖到此处，或
                        <em>点击上传</em>
                        <br>
                        <p class="upload-size">
                          {{ uploadItem.restriction.video_restriction.min_width || uploadItem.restriction.video_restriction.width }}px * {{ uploadItem.restriction.video_restriction.min_height || uploadItem.restriction.video_restriction.height }}px <br>
                          <span>（推荐尺寸）</span>
                        </p>
                      </div>
                    </div>
                    <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.video_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.video_restriction.file_size }}kb</div>
                  </div>
                  <el-progress :percentage="uploadItem.progressbar.percentage" :status="uploadItem.progressbar.status"></el-progress>
                </div>
              </div>
              <!--  视频 end  -->
              <!--  图片 begin -->
              <div v-if="tabItem.elementsData.imageList.length > 0 " class="uploadList-wrap">
                <!-- <div class="upload-li" v-for="(uploadItem, index) in tabItem.elementsData.imageList" :key="uploadItem.name+index" @click="uploadFileLiclick(index,'image')">
                  <el-upload class="upload-demo" :limit="uploadlimit" :name="uploadItem.name" :on-change="uploadImgchange" :before-upload="beforeUpload" :data="upadlodData" :headers="uploadHeaders" :on-preview="uploadFile" drag action="/Admin/Marketing_Images/add">
                    <img style="display:none" v-bind:src="imgurl" />
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">
                      将文件拖到此处，或
                      <em>点击上传</em>
                      <br>
                      <p class="upload-size">
                        {{ uploadItem.restriction.image_restriction.width }}px * {{ uploadItem.restriction.image_restriction.height }}px <br>
                        <span>（推荐尺寸）</span>
                      </p>
                    </div>
                    <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.image_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.image_restriction.file_size }}kb</div>
                  </el-upload>
                </div> -->
                <div class="uploadList-li" v-for="(uploadItem, index ) in tabItem.elementsData.imageList" :key="uploadItem.name+index">
                  <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'image')" type="file" />
                  <div class="uploadfile-item">
                    <div class="uploadfile-preview" v-if="uploadItem.restriction.image_restriction.width/uploadItem.restriction.image_restriction.height < (360/191)  ">
                      <img v-if="uploadItem.restriction.image_restriction.width<uploadItem.restriction.image_restriction.height" height="130px" :src="uploadItem.preview_url" />
                      <img v-else height="130px" :src="uploadItem.preview_url" />
                    </div>
                    <div class="uploadfile-preview" v-else>
                      <img height="130px" :src="uploadItem.preview_url" />
                    </div>
                    <div v-if="!uploadItem.preview_url">
                      <i class="el-icon-upload icon-upload"></i>
                      <div class="el-upload__text">
                        将图片文件拖到此处，或
                        <em>点击上传</em>
                        <br>
                        <p class="upload-size">
                          {{ uploadItem.restriction.image_restriction.width }}px * {{ uploadItem.restriction.image_restriction.height }}px <br>
                          <span>（推荐尺寸）</span>
                        </p>
                      </div>
                    </div>
                    <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.image_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.image_restriction.file_size }}kb</div>
                  </div>
                  <el-progress :percentage="uploadItem.progressbar.percentage" :status="uploadItem.progressbar.status"></el-progress>
                </div>
              </div>
              <!--  图片 end  -->
              <!-- 动画效果 begin -->
              <div v-if="tabItem.elementsData.enumList.length > 0 " v-for="(uploadItem,index) in tabItem.elementsData.enumList" :key="uploadItem.name+index" class="uploadurl-input">
                <el-select v-model="uploadItem.enumvalue" :placeholder="uploadItem.description">
                  <el-option v-for="(enumItem,index) in uploadItem.enum_property.enumeration" :key="enumItem.value" :value="enumItem.value" :label="enumItem.description"></el-option>
                </el-select>
              </div>
              <!-- 动画效果 begin -->
              <!-- 文本 begin -->
              <div v-if="tabItem.elementsData.textList.length > 0 " class="uploadurl-input" v-for="(uploadItem,index) in tabItem.elementsData.textList" :key="uploadItem.name+index">
                <el-input :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.txtvalue" clearable></el-input>
              </div>
              <!-- 文本 end -->
              <!-- URL begin -->
              <div v-if="tabItem.elementsData.urlList.length > 0 " class="uploadurl-input" v-for="(uploadItem,index) in tabItem.elementsData.urlList" :key="uploadItem.name+index">
                <el-input :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.url" clearable></el-input>
              </div>
              <!-- URL end -->

            </el-tab-pane>
          </el-tabs>

        </div>
      </div>
      <!--上传创意 end -->

      <el-form-item>
        <el-button type="primary" :loading="submitLoading" @click="onSubmit"> 确认修改</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>

  </div>

</template>



<script>
export default {
  created: function() {
    this.getAdCommongetEnum()
    var arr,
      reg = new RegExp('(^| )PHPSESSID=([^;]*)(;|$)')
    if ((arr = document.cookie.match(reg))) {
      this.uploadHeaders = {
        'VUE-SESSID': unescape(arr[2])
      }
    }
    if (this.account_id) {
      // 获取页面id
      var params = this.$route.params || {}
      if (params.hasOwnProperty('id')) {
        // 查询 创意
        this.getAdcreatives(params.id)
        this.adcreativeId = params.id
      }
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getAdCampaigns(this.adCampaignsParams)
      // 获取页面id
      var params = this.$route.params || {}
      if (params.hasOwnProperty('id')) {
        this.getAdcreatives(params.id) // 查询 创意
        this.adcreativeId = params.id
      }
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      submitLoading: false, // 禁止重复提交 -- loading 状态
      adcreativeId: 0, // 创意编辑id
      activeAdcreative: {}, // 创意编辑

      loadingCampaign: false,
      loading: false,
      productList: [],
      productTypeList: [],
      productShow: false,
      destinationUrlShow: false,
      uploadlimit: 1,
      upadlodData: {
        account_id: 10
      },
      uploadHeaders: {},
      uploadList: [
        {
          previewImgUrl: '',
          fileList: [
            {
              name: '',
              url: ''
            }
          ]
        }
      ],
      ADCREATIVE_STYLE_list: {},
      adcreativeList: [],
      activeAdcreativeList: false,
      Adcreativeinfo: {
        product_type: '',
        site_set: ''
      },
      adCampaignsList: [],
      site_setList: {},
      form: {
        campaign_id: '',
        product_refs_id: '',
        product_type: '',
        destination_url: '',
        site_set: '',
        adcreative_template_id: '',
        adcreative_elements: []
      },
      adCampaignsParams: {
        page: 1,
        page_size: 100,
        campaign_id: '',
        configured_status: '',
        campaign_name: '',
        start_date: '',
        end_date: ''
      },
      adcreativeTemplateInfo_row: {
        adcreative_sample_image: {}
      },
      tableDropdown: '下拉菜单',
      tabActiveName: '0',
      tabActiveList: [],
      tabList: [
        {
          label: '创意1',
          name: '0',
          adcreative_name: '',
          elementsData: {
            imageList: [], // 图片
            textList: [], // 文本
            videoList: [], // 视频
            urlList: [], // 链接
            enumList: [], // 选择器
            canvasList: [], // 画布
            referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
          }
        }
        // {
        //   label: '+ 添加',
        //   name: 'add',
        //   adcreative_name: '',
        //   elementsData: {
        //     imageList: [], // 图片
        //     textList: [], // 文本
        //     videoList: [], // 视频
        //     urlList: [], // 链接
        //     enumList: [], // 选择器
        //     canvasList: [], // 画布
        //     referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
        //   }
        // }
      ]
    }
  },
  methods: {
    // 根据创意id 查询创意列表
    getAdcreatives(id) {
      var that = this
      var params = {
        adcreative_id: id,
        account_id: that.account_id
      }
      this.$axios
        .get('/Admin/Marketing_Adcreatives/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            var adcreative = respData.data.list[0]
            that.activeAdcreative = respData.data.list[0]
            var formData = {
              campaign_id: adcreative.campaign_id,
              site_set: adcreative.site_set[0],
              product_refs_id: adcreative.product_refs_id,
              product_type: adcreative.product_type,
              product_type_name: adcreative.product_type_name,
              destination_url: adcreative.destination_url,
              adcreative_template_id: adcreative.adcreative_template_id,
              adcreative_elements: []
            }
            // 默认参数
            that.form = formData
            // 查询创意规格
            that.Adcreativeinfo.product_type = adcreative.product_type
            that.Adcreativeinfo.site_set = adcreative.site_set[0]
            that.adCampaignsParams.campaign_id = adcreative.campaign_id
            that.getAdCampaigns(that.adCampaignsParams)
          } else {
            that.$message(respData.msg)
          }
          // that.loading = false
        })
        .catch(function(err) {
          // that.loading = false
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    remoteCampaign(query) {
      // if (query !== '') {
        this.loadingCampaign = true
        setTimeout(() => {
          this.adCampaignsParams.campaign_name = query
          this.getAdCampaigns(this.adCampaignsParams)
        }, 300)
      // } else {
      //   this.adCampaignsList = []
      // }
    },
    // 点击触发
    uploadFileLiclick(index, type) {
      this.tabActiveList = {
        index: index,
        type: type
      }
    },
    // 单图片和视频上传
    uploadfileChange(e, index, tabIndex, type) {
      // console.log(index, tabIndex)
      var that = this // vm
      var target = e.target || e.currentTarget
      var file = target.files[0],
        tabTarget = {}
      var height, width, file_size, file_format
      if (type == 'image') {
        tabTarget = that.tabList[tabIndex]['elementsData'].imageList[index]
        var restriction = tabTarget.restriction
        ;(height = restriction.image_restriction.height),
          (width = restriction.image_restriction.width),
          (file_size = restriction.image_restriction.file_size),
          (file_format = restriction.image_restriction.file_format)
      } else if (type == 'video') {
        tabTarget = that.tabList[tabIndex]['elementsData'].videoList[index]
        var restriction = tabTarget.restriction
        ;(height =
          restriction.video_restriction.min_height ||
          restriction.video_restriction.height),
          (width =
            restriction.video_restriction.min_width ||
            restriction.video_restriction.width),
          (file_size = restriction.video_restriction.file_size),
          (file_format = restriction.video_restriction.file_format)
      }
      if (!file) {
        // console.log(type + ':' + file)
        return false
      }

      // 格式校验
      if (file.size / 1024 > file_size) {
        this.$message('上传文件不能超过' + file_size + 'kb')
        return false
      }
      if (!this.formatCheck(file_format, file.type, type)) {
        this.$message('上传文件格式不正确')
        return false
      }
      //  根据类型 判断 宽高限制
      if (type == 'image') {
        //读取图片数据
        var reader = new FileReader()
        reader.onload = function(e) {
          var data = e.target.result
          //加载图片获取图片真实宽度和高度
          var image = new Image()
          image.onload = function() {
            var w = image.width
            var h = image.height
            if (width == w && height == h) {
              that.uploadFileAction(file, index, tabIndex, type)
            } else {
              that.$message('上传文件尺寸不正确')
            }
          }
          image.src = data
        }
        reader.readAsDataURL(file)
      } else if (type == 'video') {
        var reader = new FileReader()
        var fileType = file.type
        reader.onload = function(e) {
          var data = e.target.result
          var video = document.createElement('video')
          document.body.appendChild(video)
          video.addEventListener('loadedmetadata', function(e) {
            if (video.videoWidth == width && video.videoHeight == height) {
              that.uploadFileAction(file, index, tabIndex, type)
            } else {
              that.$message('上传文件尺寸不正确')
            }
            video.remove()
          })
          video.setAttribute('src', data)
        }
        reader.readAsDataURL(file)
      }
    },

    //
    uploadFileAction(file, index, tabIndex, type) {
      var that = this,
        targetList,
        url = ''
      if ('image' == type) {
        url = '/Admin/Marketing_Images/add'
        targetList = that.tabList[tabIndex]['elementsData'].imageList[index]
      } else if ('video' == type) {
        url = '/Admin/Marketing_Videos/add'
        targetList = that.tabList[tabIndex]['elementsData'].videoList[index]
      }
      var fd = new FormData()
      fd.append('account_id', that.account_id)
      fd.append('file', file)
      if ('video' == type) {
        fd.append('description', file)
      }
      var xhr = new XMLHttpRequest()
      xhr.upload.addEventListener(
        'progress',
        function(evt) {
          //console.log('progressprogressprogressprogressprogress')
          if (evt.lengthComputable) {
            targetList.progressbar.percentage = 60
            Math.round(evt.loaded * 100 / evt.total)
            targetList.progressbar.status = 'success'
          } else {
            if (complete) {
              targetList.progressbar.status = 'success'
            }
          }
        },
        false
      )
      xhr.addEventListener(
        'load',
        function(evt) {
          //console.log('服务器响应：', evt)
        },
        false
      )
      xhr.addEventListener(
        'error',
        function(evt) {
          targetList.progressbar.status = 'exception'
          console.log(evt)
        },
        false
      )
      xhr.addEventListener(
        'abort',
        function() {
          console.log(evt)
        },
        false
      )
      xhr.open('POST', url) //修改成自己的接口
      xhr.setRequestHeader('VUE-SESSID', this.uploadHeaders['VUE-SESSID'])
      xhr.send(fd)
      xhr.onreadystatechange = function() {
        if (xhr.readyState == 4) {
          if (xhr.status == 200) {
            var data = JSON.parse(xhr.response)
            if (data.code == 0) {
              // console.log('onreadystatechange:', data)
              targetList.progressbar.percentage = 100
              targetList.progressbar.status = 'success'
              // console.log(imageList)
              targetList.preview_url = data.data.preview_url
              if ('video' == type) {
                targetList.preview_url_id = data.data.video_id
              } else {
                targetList.preview_url_id = data.data.image_id
              }
            } else {
              that.$message(data.msg)
              targetList.progressbar.status = 'exception'
            }
          } else {
            targetList.progressbar.status = 'exception'
          }
        }
      }
    },

    // 上传创意文件 -- 值改变
    uploadImgchange(file, fileList) {
      // console.log('change:')
      // console.log('file:', file)
      // console.log('fileList:', fileList)
    },

    // 上传创意文件之前调用
    beforeUpload(file) {
      var that = this
      // console.log('上传创意文件之前调用:', file)
      // console.log(this.tabActiveList)
      var tabActiveName = parseInt(this.tabActiveName)
      if (this.tabActiveList && this.tabActiveList.type == 'image') {
        var imageList = this.tabList[tabActiveName]['elementsData'][
          'imageList'
        ][this.tabActiveList.index]
        //console.log('imageList:', imageList)

        var restriction = imageList.restriction
        var file_size = restriction.image_restriction.file_size,
          file_format = restriction.image_restriction.file_format,
          height = restriction.image_restriction.height,
          width = restriction.image_restriction.width
        if (file.size / 1024 > file_size) {
          this.$message('上传文件不能超过' + file_size + 'kb')
          return false
        }

        if (!this.formatCheck(file_format, file.type)) {
          this.$message('上传文件格式不正确')
          return false
        }

        //读取图片数据
        var reader = new FileReader()
        reader.onload = function(e) {
          var data = e.target.result
          //加载图片获取图片真实宽度和高度
          var image = new Image()
          image.onload = function() {
            var w = image.width
            var h = image.height
            if (width == w && height == h) {
              return true
            } else {
              that.$message('上传文件尺寸不正确')
              return false
            }
          }
          image.src = data
        }
        reader.readAsDataURL(file)
        return true
      }
    },
    // 格式校验
    formatCheck(file_format, type_format, type) {
      //console.log('图片格式校验:', type)
      var text = '',
        that = this
      for (var k in file_format) {
        if (type == 'image') {
          var format = ''
          if (that.imageTypeClassify(file_format[k]) == 'jpg') {
            format = 'jpg|jpeg'
          } else {
            format = that.imageTypeClassify(file_format[k])
          }
          text = text + format + '|'
        } else {
          text = text + that.videoTypeClassify(file_format[k]) + '|'
        }
      }
      text = text.substring(0, text.length - 1)
      //var reg = new RegExp('/(png|jpg|jpeg])$')
      var result = new RegExp('/(' + text + ')$').test(type_format)
      return result
    },
    // 上传创意--服务器回调事件
    uploadFile(file) {
      console.log('服务器回调事件:', file)
      console.log('file.response', file.response)
    },
    // 深度拷贝对象
    cloneObj(obj) {
      var str,
        newobj = obj.constructor === Array ? [] : {}
      if (typeof obj !== 'object') {
        return
      } else if (window.JSON) {
        ;(str = JSON.stringify(obj)), //系列化对象
          (newobj = JSON.parse(str)) //还原
      } else {
        for (var i in obj) {
          newobj[i] = typeof obj[i] === 'object' ? cloneObj(obj[i]) : obj[i]
        }
      }
      return newobj
    },

    //  新增创意 点击事件
    handleTbaClick(tab, event) {
      // 新增创意
      if (tab.name == 'add' && this.activeAdcreativeList) {
        var elementsData = this.cloneObj(this.tabList[tab.index].elementsData)
        this.tabList.splice(tab.index, 0, {
          label: '创意' + (parseInt(tab.index) + 1),
          name: tab.index,
          adcreative_name: '',
          elementsData: elementsData
        })
        this.tabActiveName = tab.index
      } else if (tab.name == 'add') {
        this.$message('请选择创意规格')
        this.tabActiveName = '0'
      }
    },
    // 表格数据 格式
    formatterCrtstyle(row, column, cellValue) {
      return row.adcreative_template_size + row.adcreative_template_style
    },
    // 表格数据 -- 单选
    handleCurrentChange(currentRow, oldCurrentRow) {
      console.log('currentRow:', currentRow)
      // 示例图
      this.adcreativeTemplateInfo_row = {
        adcreative_sample_image: {}
      }
      this.form.adcreative_template_id = currentRow.adcreative_template_id
      this.adcreativeTemplateInfo_row = currentRow
      this.activeAdcreativeList = true
      this.uploadElementFiltrate()
    },

    // 恢复创意默认值
    defaultCreativity() {
      this.tabActiveName = '0'
      this.tabList = [
        {
          label: '创意1',
          name: '0',
          adcreative_name: '',
          elementsData: {
            imageList: [], // 图片
            textList: [], // 文本
            videoList: [], // 视频
            urlList: [], // 链接
            enumList: [], // 选择器
            canvasList: [], // 画布
            referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
          }
        }
        // {
        //   label: '+ 添加',
        //   name: 'add',
        //   adcreative_name: '',
        //   elementsData: {
        //     imageList: [], // 图片
        //     textList: [], // 文本
        //     videoList: [], // 视频
        //     urlList: [], // 链接
        //     enumList: [], // 选择器
        //     canvasList: [], // 画布
        //     referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
        //   }
        // }
      ]
    },
    // 归类 图片格式
    imageTypeClassify(type) {
      var text = ''
      if (type == 'IMAGE_TYPE_GIF') {
        text = 'gif'
      } else if (type == 'IMAGE_TYPE_JPG') {
        text = 'jpg'
      } else if (type == 'IMAGE_TYPE_PNG') {
        text = 'png'
      } else if (type == 'IMAGE_TYPE_SWF') {
        text = 'swf'
      }
      return text
    },

    // 归类 视频格式
    videoTypeClassify(type) {
      var text = ''
      if (type == 'MEDIA_TYPE_MP4') {
        text = 'mp4'
      } else if (type == 'MEDIA_TYPE_AVI') {
        text = 'avi'
      } else if (type == 'MEDIA_TYPE_MOV') {
        text = 'mov'
      } else if (type == 'MEDIA_TYPE_FLV') {
        text = 'flv'
      }
      return text
    },

    // 上传创意 -- 上传元素筛选
    uploadElementFiltrate() {
      //console.log('上传创意 -- 上传元素筛选')
      var that = this
      let row = this.adcreativeTemplateInfo_row
      var elements = this.cloneObj(row.adcreative_elements) // 原始数据
      // 根据数据 归类
      var elementsData = {
        imageList: [], // 图片
        textList: [], // 文本
        videoList: [], // 视频
        urlList: [], // 链接
        enumList: [], // 选择器
        canvasList: [], // 画布
        referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
      }
      for (var index in elements) {
        var item = elements[index]
        if ('ELEMENT_TYPE_IMAGE' == item.element_type) {
          var text = '',
            file_format_list = item.restriction.image_restriction.file_format
          for (var i in file_format_list) {
            var type = that.imageTypeClassify(file_format_list[i])
            text = text + type + '/'
          }
          text = text.substring(0, text.length - 1)
          item.restriction.image_restriction.file_format_text = text
          item.progressbar = {
            status: '',
            percentage: 0
          }
          item.preview_url = ''
          item.preview_url_id = ''
          elementsData.imageList.push(item)
        } else if ('ELEMENT_TYPE_TEXT' == item.element_type) {
          item.txtvalue = ''
          elementsData.textList.push(item)
        } else if ('ELEMENT_TYPE_VIDEO' == item.element_type) {
          var text = '',
            file_format_list = item.restriction.video_restriction.file_format
          for (var i in file_format_list) {
            var type = that.videoTypeClassify(file_format_list[i])
            text = text + type + '/'
          }
          text = text.substring(0, text.length - 1)
          item.restriction.video_restriction.file_format_text = text
          item.progressbar = {
            status: '',
            percentage: 0
          }
          item.preview_url = ''
          item.preview_url_id = ''
          elementsData.videoList.push(item)
        } else if ('ELEMENT_TYPE_URL' == item.element_type) {
          item.url = ''
          elementsData.urlList.push(item)
        } else if ('ELEMENT_TYPE_ENUM' == item.element_type) {
          item.description = '请选择' + item.description
          item.enumvalue = ''
          elementsData.enumList.push(item)
        } else if ('ELEMENT_TYPE_CANVAS' == item.element_type) {
          elementsData.canvasList.push(item)
        } else if ('ELEMENT_TYPE_REFERENCE' == item.element_type) {
          elementsData.referenceList.push(item)
        }
      }
      this.tabList[0].elementsData = this.cloneObj(elementsData)
      // this.tabList[1].elementsData = this.cloneObj(elementsData)
      // console.log('elementsData:', elementsData)
      // console.log(this.activeAdcreative)
      this.tabList[0].adcreative_name = this.activeAdcreative.adcreative_name
      this.setTabListelementsData(
        this.tabList[0].elementsData,
        this.activeAdcreative
      )
    },
    // 编辑遍历赋值
    setTabListelementsData(elementsData, activeList) {
      var that = this
      var elements = activeList.adcreative_elements
      for (var key in elementsData) {
        // console.log('key:', elementsData[key])
        if (elementsData[key].length > 0) {
          for (var i in elementsData[key]) {
            var name = elementsData[key][i]['name']
            if (
              'ELEMENT_TYPE_IMAGE' == elementsData[key][i]['element_type'] ||
              'ELEMENT_TYPE_VIDEO' == elementsData[key][i]['element_type']
            ) {
              elementsData[key][i]['preview_url_id'] = elements[name]
              elementsData[key][i]['preview_url'] = elements[name + '_url']
            } else if (
              'ELEMENT_TYPE_URL' == elementsData[key][i]['element_type']
            ) {
              elementsData[key][i]['url'] = elements[name]
            } else if (
              'ELEMENT_TYPE_ENUM' == elementsData[key][i]['element_type']
            ) {
              elementsData[key][i]['enumList'] = elements[name]
            } else if (
              'ELEMENT_TYPE_TEXT' == elementsData[key][i]['element_type']
            ) {
              elementsData[key][i]['txtvalue'] = elements[name]
            }
          }
        }
      }
      this.tabList[0].elementsData = this.cloneObj(elementsData)
      this.tabList[0].adcreative_id = that.adcreativeId
      //console.log('elementsDataafadfafasdfadffa:', this.tabList[0])
    },
    // 渲染表头
    tableHeaderRender(createElement, { column }) {
      return (
        <el-dropdown>
          <span class="el-dropdown-link">
            下拉菜单<i class="el-icon-arrow-down el-icon--right" />
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>视频</el-dropdown-item>
            <el-dropdown-item>多图轮播</el-dropdown-item>
            <el-dropdown-item>单图(文)</el-dropdown-item>
            <el-dropdown-item>多图(文)</el-dropdown-item>
            <el-dropdown-item>随心互动</el-dropdown-item>
            <el-dropdown-item>文字链</el-dropdown-item>
            <el-dropdown-item>微动</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      )
    },
    // 选择推广计划 -- 获取campaign_type
    campaignChange(value) {
      let obj = {}
      obj = this.adCampaignsList.find(item => {
        return item.campaign_id === value
      })
      this.form.product_type = obj.product_type
      if (this.productTypeList[obj.product_type]['product_refs_id'] === 1) {
        // 需要选择标的物id
        this.productShow = true
        this.destinationUrlShow = false
        this.getProducts({ product_type: obj.product_type })
      } else {
        // 不需要选择标的物id
        this.productShow = false
        this.form.destination_url = ''
        this.destinationUrlShow = true
        this.form.product_refs_id = ''
      }
      this.Adcreativeinfo.product_type = obj.product_type
      if (this.Adcreativeinfo.site_set && this.Adcreativeinfo.product_type) {
        this.getAdcreativetemplateinfo()
      }
    },
    // 选择标的物 -- 获取落地页（即标的物介绍页面）
    productChange(value) {
      let obj = {}
      obj = this.productList.find(item => {
        return item.product_refs_id === value
      })
      this.form.destination_url = obj.product_url
    },
    // 选择投放投放媒体
    siteSetListChange(value) {
      this.Adcreativeinfo.site_set = value
      // console.log('选择投放投放媒体:', value)
      // console.log(this.form)
      if (this.Adcreativeinfo.site_set && this.Adcreativeinfo.product_type) {
        this.getAdcreativetemplateinfo()
      }
    },
    // 获取创意规格 -- 请求
    getAdcreativetemplateinfo() {
      var that = this
      this.loading = true
      var params = that.Adcreativeinfo
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adcreativetemplateinfo/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            var list = []
            for (var key in respData.data.list) {
              if (
                respData.data.list[key].adcreative_template_id ==
                that.form.adcreative_template_id
              ) {
                list.push(respData.data.list[key])
                break
              }
            }
            that.adcreativeList = list
            that.$refs.adcreativeTable.setCurrentRow(list[0])
          } else {
            that.$message(respData.msg)
          }
          that.loading = false
        })
        .catch(function(err) {
          that.loading = false
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },

    onCancel() {
      this.$router.push('/advertising/adCreative')
    },
    onSubmit() {
      var params = this.form
      var that = this,
        itemList = this.tabList
      if (!params.campaign_id) {
        that.$message('请选择推广计划')
        return false
      }
      if (!params.product_type) {
        that.$message('请选择推广计划')
        return false
      }
      if (!params.site_set) {
        that.$message('请选择投放站点')
        return false
      }
      if (!this.adcreativeTemplateInfo_row.adcreative_elements) {
        that.$message('请选择创意规格')
        return false
      }

      var filtration = false,
        filtrationText = '',
        adcreative_elements = [] // 创意列表
      // 筛选校验 数据
      for (var index = 0; index < itemList.length; index++) {
        var elementsData = itemList[index].elementsData
        var elementsList = new Object() // 创意
        elementsList['adcreative_id'] = itemList[index]['adcreative_id']
        elementsList['adcreative_name'] = itemList[index]['adcreative_name']
        // 判断创意名称
        if (
          !itemList[index]['adcreative_name'] &&
          itemList[index]['adcreative_name'].length > 120
        ) {
          filtration = true
          filtrationText =
            itemList[index]['name'] + '请填写字数在(1-120)的创意名称'
          break
        }

        for (var key in elementsData) {
          if (elementsData[key].length > 0) {
            for (var k in elementsData[key]) {
              var itemTarget = elementsData[key][k]
              // 判断类型
              if (itemTarget.element_type == 'ELEMENT_TYPE_IMAGE') {
                // 图片
                // 判断是否 必填项
                if (itemTarget.required && !itemTarget.preview_url) {
                  filtration = true
                  filtrationText = '请上传图片'
                }
                // 创意
                elementsList[itemTarget.name] = itemTarget.preview_url_id
              } else if (itemTarget.element_type == 'ELEMENT_TYPE_VIDEO') {
                // 视频
                // 判断是否 必填项
                if (itemTarget.required && !itemTarget.preview_url) {
                  filtration = true
                  filtrationText = '请上传视频'
                }
                // 创意
                elementsList[itemTarget.name] = itemTarget.preview_url_id
              } else if (itemTarget.element_type == 'ELEMENT_TYPE_TEXT') {
                // 文本类型

                // 创意
                elementsList[itemTarget.name] = itemTarget.txtvalue
                if (itemTarget.required && !itemTarget.txtvalue) {
                  filtration = true
                  filtrationText = '请填写' + itemTarget.description
                } else if (itemTarget.txtvalue) {
                  if (
                    itemTarget.restriction &&
                    itemTarget.restriction.text_restriction
                  ) {
                    if (
                      itemTarget.restriction.text_restriction.max_length + 1 <
                      itemTarget.txtvalue.length
                    ) {
                      filtration = true
                      filtrationText =
                        '字符超出' +
                        itemTarget.restriction.text_restriction.max_length +
                        '个'
                    }
                  }
                }
              } else if (itemTarget.element_type == 'ELEMENT_TYPE_URL') {
                // URL

                // 创意
                elementsList[itemTarget.name] = itemTarget.url
                if (itemTarget.required && !itemTarget.url) {
                  filtration = true
                  filtrationText = itemTarget.description + '不能为空'
                } else if (itemTarget.url) {
                  if (
                    itemTarget.restriction &&
                    itemTarget.restriction.url_restriction
                  ) {
                    var url_pattern =
                      itemTarget.restriction.url_restriction.url_pattern
                    var reg = new RegExp(url_pattern)
                    if (!reg.test(itemTarget.url)) {
                      filtration = true
                      filtrationText = itemTarget.description + '格式不正确'
                    }
                  }
                }
              } else if (itemTarget.element_type == 'ELEMENT_TYPE_ENUM') {
                // 枚举类型
                // 创意
                elementsList[itemTarget.name] = itemTarget.enumvalue
                if (itemTarget.required && !itemTarget.enumvalue) {
                  filtration = true
                  filtrationText = itemTarget.description
                }
              }
            }
          }
        }
        // 添加多个创意
        adcreative_elements.push(elementsList)
      }
      //  必填项 校验
      if (filtration) {
        that.$message(filtrationText)
        return false
      }
      // 添加创意列表
      params.adcreative_elements = adcreative_elements

      //  添加创意请求
      params.account_id = this.account_id
      that.submitLoading = true
      // console.log('添加创意请求:', params)
      this.$axios
        .get('/Admin/Marketing_Adcreatives/update', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.submitLoading = false
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/advertising/adCreative')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.submitLoading = false
          console.log(err)
        })
    },

    // 公共枚举
    getAdCommongetEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.site_setList = respData.data['SITE_SET'] // 站点
            that.ADCREATIVE_STYLE_list = respData.data['ADCREATIVE_STYLE']
            that.productTypeList = respData.data['PRODUCT_TYPE']
            // console.log(that.site_setList)
            // console.log(that.ADCREATIVE_STYLE_list)
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },

    // 获取标的物
    getProducts: function(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Products/list', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.productList = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    //  获取所属计划
    getAdCampaigns: function(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.adCampaignsList = respData.data.list
              that.loadingCampaign = false
              //that.page_info = respData.data.page_info
            }
            if (params.campaign_id != '') {
              that.campaignChange(params.campaign_id)
            }
          } else {
            that.$message(respData.msg)
            that.loadingCampaign = false
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loadingCampaign = false
        })
    }
  }
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}

.sample_image_info {
  padding: 20px 0 0 0;
  background: #f8f9fa;
  text-align: center;
}

.sample_image_info p {
  color: #555;
  font-size: 14px;
  text-align: center;
}

.sample_image_info p span {
  color: #999;
  font-size: 12px;
}

.sample_image_info span {
  color: #555;
  font-size: 13px;
  text-align: center;
}

.upload-size {
  font-size: 16px;
  color: #ff5722;
}

.upload-size span {
  font-size: 12px;
  color: #999;
}

.uploadList-wrap {
  width: 100%;
  margin-bottom: 20px;
  float: left;
  position: relative;
  z-index: 2;
}

.upload-li {
  display: inline-block;
}

.uploadList-wrap .uploadList-li {
  float: left;
  width: 360px;
  min-height: 191px;
  display: inline-block;
  border: 1px dashed #d9d9d9;
  background: #fff;
  box-sizing: border-box;
  border-radius: 6px;
  text-align: center;
  cursor: pointer;
  position: relative;
  z-index: 2;
  padding-bottom: 10px;
  margin-right: 20px;
}

.upload-demo {
  width: 360px;
  display: inline-block;
  margin-right: 20px;
}

.uploadurl-input {
  width: 600px;
  margin-bottom: 15px;
}

.uploadfile-item {
  position: relative;
  z-index: 3;
}

.uploadfile-item .icon-upload {
  font-size: 57px;
  color: #c0c4cc;
}

.uploadfile-input {
  position: absolute;
  z-index: 5;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.uploadfile-preview {
  width: 100%;
}
.uploadfile-preview img {
  margin-top: 10px;
}
</style>



